<template>
  <div class="index">
    <!-- 头部导航 -->
    <!-- <div class="head" :class="{headP:$route.name=='trackEvents'}">
        <span>主管部门教育数据AI智能大屏</span>
    </div> -->
    <!-- 内容 -->
    <router-view />
  </div>
</template>

<script>
import axios from 'axios'
export default {
  components: {},

  data () {
    return {
      // 激活导航索引
      activeIndex: 0,
      city: '北京',
      weatherList: [],
      dateInfo: '',

      // 导航
      navList: [
        {label:'主管部门', value:'executive' },
      ]
    }
  },

  wacth: {},

  created () {
    const that = this
    const name = this.$route.name
    for (const i in this.navList) {
      if (this.navList[i].value === name) {
        this.activeIndex = Number(i)
      }
    }
    this.dateInit()
    setInterval(function () {
      that.dateInit()
    }, 1000)
    this.getWeather()
  },

  computed: {},

  methods: {

    // 路由跳转
    topath (index, name) {
      this.activeIndex = index
      this.$router.push({ name, query: this.params })
    },
    getWeather () {
      var that = this
      axios.get('https://www.tianqiapi.com/api/?appid=84823855&appsecret=zZG440Am&version=v1&cityid=101270101')
        .then(function (res) {
          that.weatherList = res.data.data[0]
        }).catch(() => {})
    },
    dateInit: function () {
      const date = new Date()
      const Y = date.getFullYear()
      const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1)
      const D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      const h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      const m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      const s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      const weekArr = ['周日', '周一', '周二', '周三', '周四', '周五', '周六']
      this.dateInfo = Y + '年' + M + '月' + D + '日' + ' ' + weekArr[date.getDay()] + ' ' + h + ':' + m + ':' + s
    }
  }
}

</script>
